@use '../abstracts/' as u;

nav {
  --animation-timing-function: linear(
    0,
    0.002 0.5%,
    0.008 1.1%,
    0.017 1.6%,
    0.031 2.2%,
    0.049 2.8%,
    0.07 3.4%,
    0.098 4.1%,
    0.129 4.8%,
    0.184 5.9%,
    0.257 7.2%,
    0.551 12.1%,
    0.671 14.2%,
    0.735 15.4%,
    0.789 16.5%,
    0.839 17.6%,
    0.881 18.6%,
    0.923 19.7%,
    0.957 20.7%,
    0.99 21.8%,
    1.019 22.9%,
    1.043 24%,
    1.063 25.1%,
    1.08 26.2%,
    1.094 27.4%,
    1.107 29%,
    1.114 30.7%,
    1.116 32.5%,
    1.112 34.5%,
    1.105 36.1%,
    1.095 37.9%,
    1.041 45.8%,
    1.018 49.9%,
    1.008 52.1%,
    1 54.4%,
    0.994 56.7%,
    0.99 59.1%,
    0.987 62.3%,
    0.987 65.9%,
    0.999 84.9%,
    1
  );
  --menu-item-height: calc(1.75rem + 2rem);
  --nav-closed: calc(-7 * var(--menu-item-height));
  --nav-opened: calc(-1 * var(--menu-item-height));

  position: relative;
  margin-block-end: 2rem;

  .navbar {
    position: relative;
    @include u.flex-container(row, space-between, null);
    padding: 1rem;
    background-color: var(--background);
    z-index: 99;
  }

  .logo img,
  .menu-btn {
    width: 55px;
  }

  // Burger Opened

  .menu-btn {
    position: relative;
    z-index: 100;

    &__burger,
    &__burger::before,
    &__burger::after {
      width: 40px;
      height: 2px;
      border-radius: 2px;
      margin-inline: auto;
      background-color: var(--text);
      transition: all 0.4s ease-in-out;
    }

    &__burger {
      position: relative;
      display: block;

      &::before,
      &::after {
        @include u.pseudo;
      }

      &::before {
        transform: translate(-20px, -11px);
      }

      &::after {
        transform: translate(-20px, 11px);
      }
    }
  }

  // Burger Closed

  &:has(.menu[aria-expanded='true']) .menu-btn > * {
    width: 0;
    background: transparent;

    &::before {
      transform: translate(-20px, 0px) rotate(405deg);
    }
    &::after {
      transform: translate(-20px, 0px) rotate(-405deg);
    }
  }
}

// Menu
nav {
  .menu {
    display: none;
    position: absolute;
    width: 100vw;
    padding-block: var(--menu-item-height) var(--padding-outline);
    border-radius: 0 0 var(--border-radius) var(--border-radius);
    box-shadow: 7px 12px var(--text);
    background: var(--background);
    text-align: center;

    overflow: hidden;
    z-index: 98;

    &__link {
      display: block;
      padding: 1rem;
      letter-spacing: 0.125rem;
      font-size: 1.5rem;

      &:active,
      &:hover {
        background-color: hsl(from var(--accent) h s l / 0.5);
      }
    }

    &[aria-expanded='false'] {
      display: block;
      animation: close-nav 0.65s forwards;
    }

    &[aria-expanded='true'] {
      display: block;
      animation: open-nav 0.65s forwards;
    }

    &[aria-expanded='false'],
    &[aria-expanded='true'] {
      animation-timing-function: var(--animation-timing-function);
    }
  }

  & + .nav-overlay {
    position: fixed;
    inset: 0;
    backdrop-filter: blur(5px);
    opacity: 0;
    visibility: hidden;
    transition: visibility 0.2s ease-in-out, 0.2s ease-in-out;
    z-index: 10;
  }

  &:has(.menu[aria-expanded='true']) + .nav-overlay {
    background-color: hsla(0, 0%, 0%, 0.8);
    opacity: 1;
    visibility: visible;
  }
}

// COLOR PALETTE

.color-palette {
  $color-palette-nav: var(--text), var(--accent), var(--secondary),
    var(--primary), var(--saturated);

  display: none;
  align-items: center;
  min-width: calc(45px * 5);
  margin-inline: auto 0;

  &::after {
    content: '';
    width: 40px;
    height: 40px;
    border-radius: 12px;
    background-color: var(--text);
    box-shadow: 45px 0 var(--accent), 90px 0 var(--secondary),
      135px 0 var(--primary), 180px 0 var(--saturated);
  }
}

// Nav Animations

@keyframes open-nav {
  from {
    transform: translateY(var(--nav-closed));
  }
  to {
    transform: translateY(var(--nav-opened));
  }
}

@keyframes close-nav {
  from {
    transform: translateY(var(--nav-opened));
  }
  to {
    transform: translateY(var(--nav-closed));
  }
}
